@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{list-style-type:none}body{font-family:'Montserrat', sans-serif}p{color:white}

.navbar{padding:1.5rem 15rem !important;z-index:2000;-webkit-transition:background-color .85s, -webkit-transform .85s;transition:background-color .85s, -webkit-transform .85s;transition:background-color .85s, transform .85s;transition:background-color .85s, transform .85s, -webkit-transform .85s}.navbar .navbar-nav a{font-size:25px !important}

.c-homepage-container{position:relative;height:100vh;width:100%;display:flex;align-items:center;justify-content:center}.c-homepage-container:before{content:"";background-image:url(/Portfolio/static/media/bg.0e16ac18.jpg);background-size:cover;position:absolute;background-color:black;top:0px;right:0px;bottom:0px;left:0px;z-index:1}.c-homepage-container:after{position:absolute;background:rgba(0,0,0,0.4);width:100%;height:100%;left:0;top:0;content:"";z-index:1}.c-homepage-container .styles_typicalWrapper__1_Uvh{font-size:50px;color:white;font-weight:bold}.c-homepage-container__row{display:flex;flex-direction:column;align-items:center;z-index:2}.c-homepage-container__row h2{color:white;font-size:40px;margin-bottom:20px}

.c-button{display:inline-block;font-weight:600;line-height:1.5;color:#212529;text-align:center;text-decoration:none;vertical-align:middle;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background-color:transparent;border:1px solid transparent;padding:6px 12px;font-size:17px;border-radius:4px;-webkit-transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;text-transform:uppercase}.c-button.light{color:#000;background-color:#f8f9fa;border:1px solid #f8f9fa}.c-button.light:hover{color:#f8f9fa;background-color:#000}.c-button.black{color:#f8f9fa;background-color:#000;border:1px solid #f8f9fa}.c-button.black:hover{color:#000;background-color:#f8f9fa;border:1px solid #f8f9fa}.c-button.radius{border-radius:15px}.c-button.xl{font-size:20px;padding:10px 16px}

.c-about{background-color:#212121;width:100%;height:100%}.c-about__container{padding:125px 200px}.c-about__row{display:flex;align-items:center;justify-content:space-between}.c-about__left{flex:0 0 49%;max-width:49%}.c-about__left h2,.c-about__left h3,.c-about__left p{color:white}.c-about__left h3{opacity:0.5}.c-about__left p{opacity:0.9}.c-about__left b{font-size:18px}.c-about__right{flex:0 0 49%;max-width:49%;display:flex;flex-wrap:wrap}.c-about__right-box{width:100%;margin-bottom:25px}.c-about__right-box p{font-size:19px;margin:5px !important;color:white;opacity:0.9}.c-about__button{margin-top:40px}

@-webkit-keyframes flasher{from{opacity:0.1}to{opacity:1}}@keyframes flasher{from{opacity:0.1}to{opacity:1}}.c-portfolio{background-color:black;height:200vh;width:100%}.c-portfolio p,.c-portfolio h1{color:white}.c-portfolio__container{padding:200px 200px}.c-portfolio__title{display:flex;justify-content:center;align-items:center;margin-bottom:40px}.c-portfolio__title h2{color:white;width:50%;text-align:center;border-bottom:1px solid white;line-height:0.1em;margin:10px 0 20px}.c-portfolio__title h2 span{background:#000;padding:0 35px}.c-portfolio__tab{display:flex;justify-content:center;align-items:center;margin-bottom:40px}.c-portfolio__tab-item{margin:0 20px}.c-portfolio__row{display:flex;flex-wrap:wrap}.c-portfolio__item{margin:1%;cursor:pointer;-webkit-animation:flasher 0.5s;animation:flasher 0.5s}.c-portfolio__item-img{position:relative;overflow:hidden}.c-portfolio__item-img img{width:300px;height:300px;height:auto;vertical-align:middle;border-style:none;z-index:2}.c-portfolio__item-img:hover .c-portfolio__item-overlay{opacity:.85}.c-portfolio__item-overlay{background:#0C0C0C;color:#eee;position:absolute;top:5px;left:5px;right:5px;bottom:5px;color:#35424C;opacity:0;-webkit-transition:all .5s;transition:all .5s;z-index:3;display:flex;align-items:center;justify-content:center;border:5px solid white}.c-portfolio__item-overlay-info{width:100% !important;display:flex;align-items:center;justify-content:center}.c-portfolio__item-overlay-info h6{-webkit-transform:translateY(10px);transform:translateY(10px);-webkit-transition:all .5s;transition:all .5s;font-size:30px;font-weight:900;color:white}

.c-portfolio-detail{background-color:#343a40;width:100%;height:100vh;padding:100px}.c-portfolio-detail__title h1{color:white}

